@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);
a.home-option{color:#1A1A1A;text-decoration:inherit;display:flex}a.home-option svg{padding:2px 5px 0 5px}

.kanever-logo svg{width:280px;height:280px;border-radius:50%;background-color:white}.kanever-logo .st0{fill:#AAAAAA}.kanever-logo .st1{fill:#FFFFFF}.kanever-logo .st2{fill:#E8C52E}

.home{background:#FFD029;height:1080px;display:flex;flex-direction:column;align-items:center}.home .logo{margin-top:40px;margin-bottom:80px}.home h1{color:white;margin-top:100px}.home div:last-child{display:flex;justify-content:space-between;align-self:stretch;margin:0 13%}@media (max-width: 480px){.home h1{margin-top:70px}.home .logo{margin-top:25px;margin-bottom:50px}.home div:last-child{flex-direction:column;justify-content:left;grid-gap:20px;gap:20px}}


.video{border:4px solid #FFD029}.video iframe{position:relative;margin-bottom:-30px;margin-right:-30px;padding:10px}@media (min-width: 480px){iframe{width:448px;height:252px}}

.external-videos{display:flex;flex-wrap:wrap;justify-content:center;grid-gap:30px;gap:30px}



.content{font-size:18px}.content-wrap{width:85%;margin:0 auto}

html{scroll-behavior:smooth}body{margin:0;font-family:'Varela', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1{font-size:48px;font-weight:400;line-height:1;margin:0}

